import React from "react"
import pic1 from "./mozart.jpg"
import pic2 from "./Beethoven.jpg"
import pic3 from "./サイレントマジョリティー.png"

const Head = () =>{
    return(
        <section className="hero is-primary is-light">
  <div className="hero-body">
    <div className="container">
      <h1 className="title">
        MUSIC
      </h1>
      <h2 className="subtitle">
        piano
      </h2>
    </div>
  </div>
</section>
    );
}


const Base = () =>{
    return(
<div className="tile is-ancestor">
  <div className="tile is-vertical is-8">
    <div className="tile">
      <div className="tile is-parent is-vertical">
        <article className="tile is-child box">
           <p class="title">About</p>
           <p class="subtitle">
               I used to learn the piano from 6 to 17 years old. 
               I often play the piano even now.
               Although I am very familiar with music, 
               I'm going to talk about my favorite songs and 
               how to enjoy the piano these days!!
           </p>
        </article>
        <article className="tile is-child box">
           <p class="title">My Favorite Classic1</p>
           <p class="subtitle">
               I recommend Sonata for 2 Pianos in D major, K.448 
               by Mozart. Mozart composed this song 
               in Vienna when he was 25. It is bright and light song, 
               There is a lot of interaction between 
               the first and second pianos, 
               which is fun for the performer but difficult to match.
               Recently, I have created a new way of enjoying playing
               the second piano by recording the first piano
               with an electronic piano.

           </p>
           <figure className="image is-2by3">
            <img src={pic1} alt="mozart"></img>
        </figure>
        </article>
      </div>
      <div className="tile is-parent">
        <article className="tile is-child box">
           <p class="title">My Favorite Classic2</p>
           <p class="subtitle">
                Next, I recommend 
                Piano Sonata No. 8 in C Minor, Op. 13 'Pathetique' 
                by Beethoven. This song is composed from 1798 
                to 1799. It's a slow and painful song.
                The image of sunset is perfect for this song.
                There is Adagio cantabile, which means to sing gently.
                There are some difficult parts, but it is easy to try 
                because the tempo is slow and 
                the length of the song is short.
                It's a very nice song that will cleanse our heart.
           </p>
           <figure className="image is-2by3">
            <img src={pic2} alt="beethoven"></img>
        </figure>
        </article>
      </div>
    </div>
    <div className="tile is-parent">
      <article className="tile is-child box">
        <p class="title">Recommend</p>
        <p class="subtitle">
            There is drama that is perfect for enjoying 
            classical music. It's Nodame Cantabile.
            The stage of this story is Music College.
            There are a lot of orchestral and piano songs in this drama, 
            but all of them are wonderful and emotional songs.
            The two songs mentioned above are the songs used 
            in this drama.
            It is an interesting drama that can be enjoyed 
            by people who are not familiar with classical music.
        </p>
      </article>
    </div>
  </div>
  <div className="tile is-parent">
    <article className="tile is-child box">
        <p class="title">Ear Copy</p>
        <p class="subtitle">
            I like classical music, but what I'm addicted recently 
            is ear copy. Ear copy is the reproduction of 
            the sound played by an instrument. 
            It's a rule to play classical music according to the score.
            On the other hand, in ear copy, you arrange and play 
            as you like.
            There is no music score and you have to think how to play 
            by yourself, so the difficulty level is high, but you can 
            enjoy the sense of accomplishment when you reproduce it.
            I also recently started writing the score to keep my own copy.
        </p>
        <figure className="image is-2by3">
            <img src={pic3} alt="silentmajority"></img>
        </figure>
    </article>
  </div>
</div>

    );
}


const Bottom = () =>{
    return(
        <section className="hero is-primary is-light">
  <div className="hero-body">
    <div className="container">
      &copy;minori
    </div>
  </div>
</section>
    )
}


const App = () =>{
    return (
        <div className="container">
            <Head />
            <Base />
            <Bottom />
        </div>
    );
};

export default App;